.Input {
    width: 100%;
    padding: 10px;
    box-sizing: border-box;
}

.Label {
    font-weight: bold;
    display: block;
    margin-bottom: 8px;
}

.InputElement {
    background-color: whitesmoke;
    outline: none;
    border: 1px solid aliceblue;
    font: inherit;
    padding: 6px 10px;
    display: block;
    width: 100%;
    box-sizing: border-box;
}

.InputElement:focus {
    outline:none;
}

.Invalid, .InputElement:invalid {
    border: 1px solid salmon;
}